<template>
  <div id="holiday-loading-container" class="vs-con-loading__container">
    <div class="inline-form p-4 mb-base" v-if="holidayFormVisible">
      <div class="nv-row mb-4">
        <div class="nv-col sm:w w-full required">
          <label class="vs-input--label">{{ $t('HOLIDAY_NAME') }}</label>
          <vs-input class="w-full" v-model="holidayName"/>
        </div>
      </div>
      <div class="nv-row mb-4">
        <div class="nv-col sm:w-1/2 w-full">
          <label class="vs-input--label">{{ $t('FROM_DATE') }}</label>
          <nv-input-group>
            <template slot="append">
              <div class="append-text bg-dark">
                <i class="far fa-calendar-alt"></i>
              </div>
            </template>
            <datepicker class="w-full" :placeholder="$t('SELECT_DATE')" v-model="fromDate" :format="dateFormat"></datepicker>
          </nv-input-group>
        </div>
        <div class="nv-col sm:w-1/2 w-full">
          <label class="vs-input--label">{{ $t('TO_DATE') }}</label>
          <nv-input-group>
            <template slot="append">
              <div class="append-text bg-dark">
                <i class="far fa-calendar-alt"></i>
              </div>
            </template>
            <datepicker class="w-full" :placeholder="$t('SELECT_DATE')" v-model="toDate" :format="dateFormat"></datepicker>
          </nv-input-group>
        </div>
      </div>
      <div class="nv-row mb-4">
        <div class="nv-col">
          <vs-checkbox class="w-full" v-model="recurring">{{ $t('RECURRING') }}</vs-checkbox>
        </div>
      </div>
      <div class="nv-row mb-4">
        <div class="nv-col">
          <vs-checkbox class="w-full" v-model="disabled">{{ $t('DISABLED') }}</vs-checkbox>
        </div>
      </div>
      <div class="nv-row">
        <div class="nv-col sm:w-1/2 w-full">
          <div class="flex justify-left">
            <vs-button class="px-5 py-3 mr-2" @click="holidaySubmitAction" color="primary" type="filled">{{ $t('SUBMIT') }}</vs-button>
            <vs-button class="px-5 py-3 mr-2" @click="hideHolidayForm" color="primary" type="border">{{ $t('CANCEL') }}</vs-button>
          </div>
        </div>
      </div>
    </div>

    <vs-table class="mb-base" :data="holidayTableData" :noDataText="holidayTableNoDataText" :sst="true" search @search="filterHolidayTable" @sort="orderHolidayTable">
      <div slot="header" class="flex flex-wrap-reverse items-center flex-grow justify-between">
        <div class="flex flex-wrap-reverse items-center">
          <vs-button class="p-3 mr-4" @click="showHolidayForm" :disabled="holidayFormVisible" color="primary" type="filled" icon="fa-plus" icon-pack="far" v-if="$acl.check('isNucleusAll')">{{ $t('ADD') }}</vs-button>
          <vs-button class="p-3 mr-4" @click="refreshHolidayTable" type="filled" icon="fa-sync" icon-pack="far" :disabled="holidayTableBusy">{{ $t('REFRESH') }}</vs-button>
        </div>

        <div class="flex justify-end mr-4">
          <div class="p-2 border border-solid border-grey-light rounded-full d-theme-dark-bg flex items-center justify-between font-medium">
            <span class="mx-2">{{ holidayTablePagingInfo }}</span>
            <vs-dropdown vs-trigger-click class="cursor-pointer mr-2">
              <span class="far fa-angle-down text-primary"></span>
              <vs-dropdown-menu>
                <vs-dropdown-item :key="index" v-for="(item, index) in tablePageOptions" @click="holidayTablePerPage = item">
                  <span>{{ item }}</span>
                </vs-dropdown-item>
              </vs-dropdown-menu>
            </vs-dropdown>
          </div>
        </div>
      </div>

      <template slot="thead">
        <vs-th sort-key="holidayName">
          {{ this.$t('HOLIDAY_NAME') }}
        </vs-th>
        <vs-th sort-key="fromDate">
          {{ this.$t('FROM_DATE') }}
        </vs-th>
        <vs-th sort-key="toDate">
          {{ this.$t('TO_DATE') }}
        </vs-th>
        <vs-th sort-key="recurring">
          {{ this.$t('RECURRING') }}
        </vs-th>
        <vs-th v-if="$acl.check('isNucleusAll')">
          {{ this.$t('OPTIONS') }}
        </vs-th>
      </template>

      <template slot-scope="{data}">
        <vs-tr :key="index" v-for="(tr, index) in data" :data="tr">
          <vs-td :data="data[index].holidayName">
            <span :class="disabledClass(data[index])">{{ data[index].holidayName }}</span>
          </vs-td>
          <vs-td :data="data[index].fromDate">
            {{ data[index].fromDate | formatUnixDate }}
          </vs-td>
          <vs-td :data="data[index].toDate">
            {{ data[index].toDate | formatUnixDate }}
          </vs-td>
          <vs-td :data="data[index].recurring">
            {{ data[index].recurring | prettyBool }}
          </vs-td>
          <vs-td v-if="$acl.check('isNucleusAll')">
            <div class="flex">
              <vs-button class="p-3 mr-4" color="primary" @click="loadHolidayUpdateForm(data[index])" icon-pack="far" icon="fa-pen">{{ $t('MODIFY') }}</vs-button>
              <vs-button class="p-3 mr-4" color="danger" @click="deleteCurrencyHoliday(data[index])" icon-pack="far" icon="fa-trash-alt">{{ $t('DELETE') }}</vs-button>
            </div>
          </vs-td>
        </vs-tr>
      </template>
    </vs-table>

    <vs-pagination :total="holidayTableTotalPages" v-model="holidayTableCurrentPage" :max="maxPaginationItems"></vs-pagination>
  </div>
</template>

<script>
import DatePickerMixin from '@/mixins/commons/DatePickerMixin'
import DisabledClassMixin from '@/mixins/commons/DisabledClassMixin'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'
import CurrencyService from '@/services/nucleus/CurrencyService'
import Datepicker from 'vuejs-datepicker'

export default {
  name: 'currency-holiday',
  mixins: [DatePickerMixin, DisabledClassMixin, LoadingMixin, ListLayoutMixin],
  components: {
    Datepicker
  },
  props: {
    uomId: {
      type: String,
      required: true
    }
  },
  data: function () {
    return {
      holidayTableData: [],
      holidayTableFilter: null,
      holidayTableFilterTimer: null,
      holidayTableOrderBy: null,
      holidayTableContainer: 'holiday-loading-container',
      holidayTableBusy: false,
      holidayTableTotalRows: 0,
      holidayTableCurrentPage: 1,
      holidayTablePerPage: 10,
      holidayFormVisible: false,
      holidayId: null,
      holidayName: '',
      fromDate: null,
      toDate: null,
      recurring: false,
      disabled: false
    }
  },
  computed: {
    holidayTableNoDataText: function () {
      return this.holidayTableBusy ? this.$t('LOADING') + ' ...' : this.$t('NO_DATA_FOUND')
    },
    holidayTableTotalPages: function () {
      return Math.ceil(this.holidayTableTotalRows / this.holidayTablePerPage)
    },
    holidayTablePagingInfo: function () {
      return this.pagingInfo(this.holidayTableCurrentPage, this.holidayTablePerPage, this.holidayTableTotalRows)
    }
  },
  methods: {
    loadHolidayTableData (loadingContainerId) {
      let self = this
      this.holidayTableBusy = true
      this.holidayTableContainer = loadingContainerId || this.holidayTableContainer
      this.startLoading(this.holidayTableContainer)
      CurrencyService.currencyHolidays({
        pageIndex: this.holidayTableCurrentPage - 1,
        pageSize: this.holidayTablePerPage,
        filter: this.holidayTableFilter,
        orderByField: this.holidayTableOrderBy,
        uomId: this.uomId
      }).then(function (response) {
        self.stopLoading(self.holidayTableContainer)
        self.holidayTableTotalRows = response.data.totalRows
        self.holidayTableData = response.data.currencyHolidayList || []
        self.holidayTableBusy = false
      })
    },
    refreshHolidayTable () {
      this.loadHolidayTableData()
    },
    filterHolidayTable (filter) {
      let self = this
      if (this.holidayTableFilter !== filter) {
        clearTimeout(this.holidayTableFilterTimer)
        this.holidayTableFilterTimer = setTimeout(() => {
          self.holidayTableFilter = filter
          self.loadHolidayTableData()
        }, process.env.VUE_APP_SEARCH_DELAY)
      } else {
        clearTimeout(this.holidayTableFilterTimer)
      }
    },
    orderHolidayTable (orderBy, active) {
      this.holidayTableOrderBy = (active ? '' : '-') + orderBy
      this.loadHolidayTableData()
    },
    showHolidayForm () {
      this.resetHolidayForm()
      this.holidayFormVisible = true
    },
    hideHolidayForm () {
      this.holidayFormVisible = false
    },
    createCurrencyHoliday () {
      let self = this
      CurrencyService.createCurrencyHoliday({
        uomId: this.uomId,
        holidayName: this.holidayName,
        fromDate: this.getTime(this.fromDate),
        toDate: this.getTime(this.toDate),
        recurring: this.recurring ? 'Y' : 'N',
        disabled: this.disabled ? 'Y' : 'N'
      }).then(function (response) {
        self.refreshHolidayTable()
        self.hideHolidayForm()
      }).catch(function (error) {
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    },
    loadHolidayUpdateForm (holiday) {
      this.holidayFormVisible = true
      this.holidayId = holiday.holidayId
      this.holidayName = holiday.holidayName
      this.fromDate = holiday.fromDate
      this.toDate = holiday.toDate
      this.recurring = holiday.recurring === 'Y'
      this.disabled = holiday.disabled === 'Y'
    },
    modifyCurrencyHoliday () {
      let self = this
      CurrencyService.modifyCurrencyHoliday({
        uomId: this.uomId,
        holidayId: this.holidayId,
        holidayName: this.holidayName,
        fromDate: this.getTime(this.fromDate),
        toDate: this.getTime(this.toDate),
        recurring: this.recurring ? 'Y' : 'N',
        disabled: this.disabled ? 'Y' : 'N'
      }).then(function (response) {
        self.refreshHolidayTable()
      }).catch(function (error) {
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    },
    deleteCurrencyHoliday (holiday) {
      let self = this
      CurrencyService.deleteCurrencyHoliday({
        holidayId: holiday.holidayId
      }).then(function (response) {
        self.refreshHolidayTable()
        self.resetHolidayForm()
        self.holidayFormVisible = false
      }).catch(function (error) {
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    },
    holidaySubmitAction () {
      if (this.holidayId) {
        this.modifyCurrencyHoliday()
      } else {
        this.createCurrencyHoliday()
      }
    },
    resetHolidayForm () {
      this.holidayId = null
      this.holidayName = ''
      this.fromDate = ''
      this.toDate = ''
      this.recurring = false
      this.disabled = false
    }
  },
  mounted () {
    this.loadHolidayTableData()
  },
  watch: {
    holidayTableCurrentPage: function () {
      this.refreshHolidayTable()
    },
    holidayTablePerPage: function () {
      this.refreshHolidayTable()
    },
    uomId: function () {
      this.refreshHolidayTable()
    }
  }
}
</script>
