<template>
  <div>
    <div class="alert-container" v-if="alertActive">
      <vs-alert :active.sync="alertActive" :color="alertVariant" closable icon-pack="feather" close-icon="icon-x" class="mb-5">
        <p>{{ alertMessage }}</p>
      </vs-alert>
    </div>

    <nv-card>
      <div class="flex flex-wrap-reverse items-center mb-base">
        <vs-button class="p-3 mr-4" @click="cancel" color="primary" type="filled" icon="fa-chevron-left" icon-pack="far">{{ $t('BACK') }}</vs-button>
        <vs-button class="p-3 mr-4" @click="modify" color="primary" type="filled" icon="fa-pen" icon-pack="far" v-if="$acl.check('isNucleusAll')">{{ $t('MODIFY') }}</vs-button>
      </div>
      <div class="row hover mb-base">
        <p class="info-row">
          <span>{{ $t('ID') }}:</span>
          <span>{{ currency.uomId }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('CURRENCY_NAME') }}:</span>
          <span :class="disabledClass(currency)">{{ currency.description }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('CURRENCY_CODE') }}:</span>
          <span>{{ currency.abbreviation | placeholder('-') }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('NUMERIC_CODE') }}:</span>
          <span>{{ currency.numericCode | placeholder('-') }}</span>
        </p>
      </div>

      <vs-tabs>
        <vs-tab :label="$t('WEEKEND')">
          <div>
            <div class="nv-row" v-if="!hasWeekend">
              <div class="nv-col w-full">
                <vs-alert active="true" color="warning" class="mb-5">
                  <i class="far fa-exclamation-circle pr-1"></i> {{ $t('NO_DATA_FOUND') }}
                </vs-alert>
                <vs-button class="p-3" @click="createCurrencyWeekend" color="primary" type="filled" icon="fa-plus" icon-pack="far" v-if="$acl.check('isNucleusAll')">{{ $t('ADD') }}</vs-button>
              </div>
            </div>

            <div v-else>
              <div class="nv-row mb-4">
                <div class="nv-col">
                  <vs-checkbox class="w-full" v-model="weekend.sunday">{{ $t('SUNDAY') }}</vs-checkbox>
                </div>
              </div>
              <div class="nv-row mb-4">
                <div class="nv-col">
                  <vs-checkbox class="w-full" v-model="weekend.monday">{{ $t('MONDAY') }}</vs-checkbox>
                </div>
              </div>
              <div class="nv-row mb-4">
                <div class="nv-col">
                  <vs-checkbox class="w-full" v-model="weekend.tuesday">{{ $t('TUESDAY') }}</vs-checkbox>
                </div>
              </div>
              <div class="nv-row mb-4">
                <div class="nv-col">
                  <vs-checkbox class="w-full" v-model="weekend.wednesday">{{ $t('WEDNESDAY') }}</vs-checkbox>
                </div>
              </div>
              <div class="nv-row mb-4">
                <div class="nv-col">
                  <vs-checkbox class="w-full" v-model="weekend.thursday">{{ $t('THURSDAY') }}</vs-checkbox>
                </div>
              </div>
              <div class="nv-row mb-4">
                <div class="nv-col">
                  <vs-checkbox class="w-full" v-model="weekend.friday">{{ $t('FRIDAY') }}</vs-checkbox>
                </div>
              </div>
              <div class="nv-row mb-4">
                <div class="nv-col">
                  <vs-checkbox class="w-full" v-model="weekend.saturday">{{ $t('SATURDAY') }}</vs-checkbox>
                </div>
              </div>

              <div class="nv-row">
                <div class="nv-col w-full">
                  <vs-button class="px-5 py-3 mr-2" @click="modifyCurrencyWeekend">{{ $t('SAVE') }}</vs-button>
                </div>
              </div>
            </div>
          </div>
        </vs-tab>

        <vs-tab :label="$t('HOLIDAYS')">
          <currency-holiday @show-alert="showAlert" :uomId="uomId"/>
        </vs-tab>

        <vs-tab :label="$t('APPS')">
          <currency-app @show-alert="showAlert" :uomId="uomId"/>
        </vs-tab>
      </vs-tabs>
    </nv-card>
  </div>
</template>

<script>
import AlertMixin from '@/mixins/commons/AlertMixin'
import DisabledClassMixin from '@/mixins/commons/DisabledClassMixin'
import DatePickerMixin from '@/mixins/commons/DatePickerMixin'
import UomService from '@/services/commons/UomService'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'
import CurrencyService from '@/services/nucleus/CurrencyService'
import CurrencyApp from '@/views/nucleus/currency/currency-view/CurrencyApp'
import CurrencyHoliday from '@/views/nucleus/currency/currency-view/CurrencyHoliday'

export default {
  name: 'currency-view',
  mixins: [AlertMixin, DatePickerMixin, DisabledClassMixin, ListLayoutMixin],
  components: {
    CurrencyApp,
    CurrencyHoliday
  },
  data: function () {
    return {
      uomId: this.$route.params.id,
      currency: {},
      weekend: {},
      hasWeekend: false
    }
  },
  methods: {
    async load () {
      let self = this
      UomService.uoms({
        uomId: this.uomId,
        uomTypeEnumId: 'UT_CURRENCY_MEASURE'
      }).then(function (response) {
        self.currency = response.data.uomList[0] || {}
      })
    },
    loadCurrencyWeekend () {
      let self = this
      CurrencyService.currencyWeekend({
        uomId: this.$route.params.id
      }).then(function (response) {
        self.weekend = response.data.weekend || {}
        if (Object.getOwnPropertyNames(self.weekend).length > 1) {
          self.weekend.sunday = self.weekend.sunday === 'Y'
          self.weekend.monday = self.weekend.monday === 'Y'
          self.weekend.tuesday = self.weekend.tuesday === 'Y'
          self.weekend.wednesday = self.weekend.wednesday === 'Y'
          self.weekend.thursday = self.weekend.thursday === 'Y'
          self.weekend.friday = self.weekend.friday === 'Y'
          self.weekend.saturday = self.weekend.saturday === 'Y'
          self.hasWeekend = true
        }
      })
    },
    createCurrencyWeekend () {
      let self = this
      CurrencyService.createCurrencyWeekend({
        uomId: this.$route.params.id,
        sunday: this.weekend.sunday ? 'Y' : 'N',
        monday: this.weekend.monday ? 'Y' : 'N',
        tuesday: this.weekend.tuesday ? 'Y' : 'N',
        wednesday: this.weekend.wednesday ? 'Y' : 'N',
        thursday: this.weekend.thursday ? 'Y' : 'N',
        friday: this.weekend.friday ? 'Y' : 'N',
        saturday: this.weekend.saturday ? 'Y' : 'N'
      }).then(function (response) {
        self.loadCurrencyWeekend()
      }).catch(function (error) {
        self.showDangerAlert(error.response.data.errors)
      })
    },
    modifyCurrencyWeekend () {
      let self = this
      CurrencyService.modifyCurrencyWeekend({
        uomId: this.$route.params.id,
        sunday: this.weekend.sunday ? 'Y' : 'N',
        monday: this.weekend.monday ? 'Y' : 'N',
        tuesday: this.weekend.tuesday ? 'Y' : 'N',
        wednesday: this.weekend.wednesday ? 'Y' : 'N',
        thursday: this.weekend.thursday ? 'Y' : 'N',
        friday: this.weekend.friday ? 'Y' : 'N',
        saturday: this.weekend.saturday ? 'Y' : 'N'
      }).then(function (response) {
        self.loadCurrencyWeekend()
        self.showSuccessAlert(response.data.messages)
      }).catch(function (error) {
        self.showDangerAlert(error.response.data.errors)
      })
    },
    cancel () {
      this.$router.back()
    },
    modify () {
      this.$router.push({ name: 'ModifyCurrency', params: { id: this.$route.params.id } })
    }
  },
  mounted () {
    this.load()
    this.loadCurrencyWeekend()
  }
}
</script>
