import Endpoint from '@/services/Endpoint'

export default {
  uoms (payload) {
    return Endpoint().get('/s1/commons/basic/uom', {
      params: payload
    })
  },
  createUom (payload) {
    return Endpoint().post('/s1/commons/basic/uom', payload)
  },
  modifyUom (payload) {
    return Endpoint().patch('/s1/commons/basic/uom', payload)
  }
}
