<template>
  <div id="app-loading-container" class="vs-con-loading__container">
    <div class="inline-form p-4 mb-base" v-if="appFormVisible">
      <div class="nv-row mb-4">
        <div class="nv-col sm:w w-full required">
          <label class="vs-input--label">{{ $t('APP') }}</label>
          <vs-select autocomplete class="w-full" v-model="appId">
            <vs-select-item v-for="(app, index) in apps" :key="index" :value="app.appId" :text="app.appName"/>
          </vs-select>
        </div>
      </div>
      <div class="nv-row">
        <div class="nv-col sm:w-1/2 w-full">
          <div class="flex justify-left">
            <vs-button class="px-5 py-3 mr-2" @click="createCurrencyApp" color="primary">{{ $t('SUBMIT') }}</vs-button>
            <vs-button class="px-5 py-3 mr-2" @click="hideAppForm" color="primary" type="border">{{ $t('CANCEL') }}</vs-button>
          </div>
        </div>
      </div>
    </div>

    <vs-table class="mb-base" :data="appTableData" :noDataText="appTableNoDataText" :sst="true" search @search="filterAppTable" @sort="orderAppTable">
      <div slot="header" class="flex flex-wrap-reverse items-center flex-grow justify-between">
        <div class="flex flex-wrap-reverse items-center">
          <vs-button class="p-3 mr-4" @click="showAppForm" :disabled="appFormVisible" color="primary" type="filled" icon="fa-plus" icon-pack="far" v-if="$acl.check('isNucleusAll')">{{ $t('ADD') }}</vs-button>
          <vs-button class="p-3 mr-4" @click="refreshAppTable" type="filled" icon="fa-sync" icon-pack="far" :disabled="appTableBusy">{{ $t('REFRESH') }}</vs-button>
        </div>

        <div class="flex justify-end mr-4">
          <div class="p-2 border border-solid border-grey-light rounded-full d-theme-dark-bg flex items-center justify-between font-medium">
            <span class="mx-2">{{ appTablePagingInfo }}</span>
            <vs-dropdown vs-trigger-click class="cursor-pointer mr-2">
              <span class="far fa-angle-down text-primary"></span>
              <vs-dropdown-menu>
                <vs-dropdown-item :key="index" v-for="(item, index) in tablePageOptions" @click="appTablePerPage = item">
                  <span>{{ item }}</span>
                </vs-dropdown-item>
              </vs-dropdown-menu>
            </vs-dropdown>
          </div>
        </div>
      </div>

      <template slot="thead">
        <vs-th sort-key="appName">
          {{ this.$t('APP_NAME') }}
        </vs-th>
        <vs-th sort-key="appDescription">
          {{ this.$t('APP_DESCRIPTION') }}
        </vs-th>
        <vs-th v-if="$acl.check('isNucleusAll')">
          {{ this.$t('OPTIONS') }}
        </vs-th>
      </template>

      <template slot-scope="{data}">
        <vs-tr :key="index" v-for="(tr, index) in data" :data="tr">
          <vs-td :data="data[index].appName">
            {{ data[index].appName }}
          </vs-td>
          <vs-td :data="data[index].appDescription">
            {{ data[index].appDescription | placeholder('-') }}
          </vs-td>
          <vs-td v-if="$acl.check('isNucleusAll')">
            <vs-button class="p-3 mr-4" color="danger" @click="deleteCurrencyApp(data[index])" icon-pack="far" icon="fa-trash-alt">{{ $t('DELETE') }}</vs-button>
          </vs-td>
        </vs-tr>
      </template>
    </vs-table>

    <vs-pagination :total="appTableTotalPages" v-model="appTableCurrentPage" :max="maxPaginationItems"></vs-pagination>
  </div>
</template>

<script>
import DisabledClassMixin from '@/mixins/commons/DisabledClassMixin'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'
import CurrencyService from '@/services/nucleus/CurrencyService'
import AppService from '@/services/commons/AppService'

export default {
  name: 'currency-app',
  mixins: [DisabledClassMixin, LoadingMixin, ListLayoutMixin],
  props: {
    uomId: {
      type: String,
      required: true
    }
  },
  data: function () {
    return {
      appTableData: [],
      appTableFilter: null,
      appTableFilterTimer: null,
      appTableOrderBy: null,
      appTableContainer: 'app-loading-container',
      appTableBusy: false,
      appTableCurrentPage: 1,
      appTablePerPage: 10,
      appTableTotalRows: 0,
      appFormVisible: false,
      appId: null,
      apps: []
    }
  },
  computed: {
    appTableNoDataText: function () {
      return this.appTableBusy ? this.$t('LOADING') + ' ...' : this.$t('NO_DATA_FOUND')
    },
    appTableTotalPages: function () {
      return Math.ceil(this.appTableTotalRows / this.appTablePerPage)
    },
    appTablePagingInfo: function () {
      return this.pagingInfo(this.appTableCurrentPage, this.appTablePerPage, this.appTableTotalRows)
    }
  },
  methods: {
    async load () {
      let self = this
      AppService.apps({
      }).then(function (response) {
        self.apps = response.data.appList || []
      })
    },
    loadAppTableData (loadingContainerId) {
      let self = this
      this.appTableBusy = true
      this.appTableContainer = loadingContainerId || this.appTableContainer
      this.startLoading(this.appTableContainer)
      CurrencyService.currencyApps({
        pageIndex: this.appTableCurrentPage - 1,
        pageSize: this.appTablePerPage,
        filter: this.appTableFilter,
        orderByField: this.appTableOrderBy,
        uomId: this.uomId
      }).then(function (response) {
        self.stopLoading(self.appTableContainer)
        self.appTableTotalRows = response.data.totalRows
        self.appTableData = response.data.currencyAppList || []
        self.appTableBusy = false
      })
    },
    refreshAppTable () {
      this.loadAppTableData()
    },
    filterAppTable (filter) {
      let self = this
      if (this.appTableFilter !== filter) {
        clearTimeout(this.appTableFilterTimer)
        this.appTableFilterTimer = setTimeout(() => {
          self.appTableFilter = filter
          self.loadAppTableData()
        }, process.env.VUE_APP_SEARCH_DELAY)
      } else {
        clearTimeout(this.appTableFilterTimer)
      }
    },
    orderAppTable (orderBy, active) {
      this.appTableOrderBy = (active ? '' : '-') + orderBy
      this.loadAppTableData()
    },
    showAppForm () {
      this.appFormVisible = true
    },
    hideAppForm () {
      this.appFormVisible = false
    },
    createCurrencyApp () {
      let self = this
      CurrencyService.createCurrencyApp({
        uomId: this.uomId,
        appId: this.appId
      }).then(function (response) {
        self.refreshAppTable()
        self.hideAppForm()
      }).catch(function (error) {
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    },
    deleteCurrencyApp (app) {
      let self = this
      CurrencyService.deleteCurrencyApp({
        uomId: this.uomId,
        appId: app.appId
      }).then(function (response) {
        self.refreshAppTable()
      }).catch(function (error) {
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    }
  },
  mounted () {
    this.load()
    this.loadAppTableData()
  },
  watch: {
    appTableCurrentPage: function () {
      this.refreshAppTable()
    },
    appTablePerPage: function () {
      this.refreshAppTable()
    },
    uomId: function () {
      this.refreshAppTable()
    }
  }
}
</script>
