import Endpoint from '@/services/Endpoint'

export default {
  currencyWeekend (payload) {
    return Endpoint().get('/s1/nucleus/basic/currency/weekend', {
      params: payload
    })
  },
  createCurrencyWeekend (payload) {
    return Endpoint().post('/s1/nucleus/basic/currency/weekend', payload)
  },
  modifyCurrencyWeekend (payload) {
    return Endpoint().patch('/s1/nucleus/basic/currency/weekend', payload)
  },
  currencyHolidays (payload) {
    return Endpoint().get('/s1/nucleus/basic/currency/holiday', {
      params: payload
    })
  },
  createCurrencyHoliday (payload) {
    return Endpoint().post('/s1/nucleus/basic/currency/holiday', payload)
  },
  modifyCurrencyHoliday (payload) {
    return Endpoint().patch('/s1/nucleus/basic/currency/holiday', payload)
  },
  deleteCurrencyHoliday (payload) {
    return Endpoint().delete('/s1/nucleus/basic/currency/holiday', {
      params: payload
    })
  },
  currencyApps (payload) {
    return Endpoint().get('/s1/nucleus/basic/currency/app', {
      params: payload
    })
  },
  createCurrencyApp (payload) {
    return Endpoint().post('/s1/nucleus/basic/currency/app', payload)
  },
  modifyCurrencyApp (payload) {
    return Endpoint().patch('/s1/nucleus/basic/currency/app', payload)
  },
  deleteCurrencyApp (payload) {
    return Endpoint().delete('/s1/nucleus/basic/currency/app', {
      params: payload
    })
  }
}
